<template>
    
<main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-id.sync="filterColorId"/>
      

      <section class="catalog">

        <div id="inTurnFadingTextG" v-if="productsLoading"><div id="inTurnFadingTextG_1" class="inTurnFadingTextG">L</div><div id="inTurnFadingTextG_2" class="inTurnFadingTextG">o</div><div id="inTurnFadingTextG_3" class="inTurnFadingTextG">a</div><div id="inTurnFadingTextG_4" class="inTurnFadingTextG">d</div><div id="inTurnFadingTextG_5" class="inTurnFadingTextG">i</div><div id="inTurnFadingTextG_6" class="inTurnFadingTextG">n</div><div id="inTurnFadingTextG_7" class="inTurnFadingTextG">g</div></div>
        <div v-if="productLoadingFailed">Opppsss что-то пошло не так... <button @click.prevent="loadProducts">Попробовать еще раз</button></div>
        
      <ProductList :products="products" @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams)"/>

      <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"/>
       
      </section>

      
    </div>
  </main>
</template>

<script> 
    import ProductList from '@/components/ProductList'
    import BasePagination from '@/components/BasePagination'
    import ProductFilter from '@/components/ProductFilter'
    import axios from 'axios'
    import { API_BASE_URL } from '@/config'

export default {

  components: {ProductList, BasePagination, ProductFilter},
  data(){
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,

      page: 1,
      productsPerPage: 3,

      productsData: null,

      productsLoading: false,
      productLoadingFailed: false
    }
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items.map(product => {
        return {...product,
                  image: product.image.file.url}}): []
    },
    countProducts(){
      return this.productsData ? this.productsData.pagination.total : 0
    }
  },
  methods: {
    loadProducts(){
      this.productsLoading = true
      this.productLoadingFailed = false
      clearTimeout(this.loadProductsTimer)
      this.loadProductsTimer = setTimeout(() => {
      axios.get(API_BASE_URL + `/api/products`,{
        params: {
          page: this.page,
          limit: this.productsPerPage,
          categoryId: this.filterCategoryId,
          minPrice: this.filterPriceFrom,
          maxPrice: this.filterPriceTo,
          colorId: this.filterColorId
        }
      })
      .then(response => this.productsData = response.data)
      .catch(() => this.productLoadingFailed = true)
      .then(() => this.productsLoading = false)
     },0)
    }
  },
  watch: {
    page(){ this.loadProducts(); },
    filterCategoryId(){ this.loadProducts(); },
    filterPriceFrom(){ this.loadProducts(); },
    filterPriceTo(){ this.loadProducts(); },
    filterColorId(){ this.loadProducts(); }
  },
  created() {
      this.loadProducts()
    }
}
</script>